<template>
  <div class="like-page">
    <van-nav-bar fixed title="我的喜欢"/>
    <van-list :v-model="loading" :finished="finished" finished-text="没有更多数据了" @load="onLoad">
      <ArticleItem v-for="item in list" :key="item.id" :item="item"></ArticleItem>
    </van-list>
  </div>
</template>

<script>
import { getArticlesLike } from '@/api/article'

export default {
  name: 'LikePage',
  data () {
    return {
      list: [],
      page: 1,
      loading: false,
      finished: false
    }
  },
  // async created () {
  //   const { data } = await getArticlesLike({ page: this.page })
  //   this.list = data.rows
  //   console.log(data)
  // },
  methods: {
    async onLoad () {
      const { data } = await getArticlesLike({ page: this.page })
      this.list.push(...data.rows)
      this.page++
      this.loading = false

      if (this.page > data.pageTotal) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.like-page {
  margin-bottom: 20px;
  margin-top: 14px;
}
</style>
